import "./App.css";
import React from "react";
import Header from "./Header";
import MyTimer from "./MyTimer";
import Music from "./Music";

function App() {

  const time = new Date();
  time.setHours(time.getHours() + 1);

  return (
    <div className = "Body">
      <Header />
      <Music />
      <MyTimer/>

    </div>
  );
}

export default App;
