import React from "react";
import Button from "@mui/material/Button";
import ButtonGroup from "@mui/material/ButtonGroup";
import Box from "@mui/material/Box";
import Divider from "@mui/material/Divider";
import "./MyTimer.css";
//eslint-disable-next-line
import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";

function MyTimer() {

  const [hours, minutes, seconds] = [0,0,0]


  const [paused, setPaused] = React.useState(false);
  const [rMinute, setRMinute] = React.useState(0);
  const [over, setOver] = React.useState(false);
  const [[h, m, s], setTime] = React.useState([hours, minutes, seconds]);

  const tick = () => {
    if (paused || over) return;
    if (h === 0 && m === 0 && s === 0) setOver(true);
    else if (m === 0 && s === 0) {
      setTime([h - 1, 59, 59]);
    } else if (s === 0) {
      setTime([h, m - 1, 59]);
    } else {
      setTime([h, m, s - 1]);
    }
  };

  const reset = () => {
    setTime([parseInt(hours), parseInt(rMinute), parseInt(seconds)]);
    setPaused(false);
    setOver(false);
  };

  React.useEffect(() => {
    const timerID = setInterval(() => tick(), 1000);
    return () => clearInterval(timerID);
  },);

  function GroupOrientation() {
    const buttons = [
      <Button
        onClick={() => {
          setTime([0,25,0]);
          setRMinute(25);
        }}
      >
        25 Minutes
      </Button>,

      <Button
        onClick={() => {         
          setTime([0,45,0]);
          setRMinute(45);
        }}
      >
        45 Minutes
      </Button>,

      <Button
        onClick={() => {
          setTime([0,5,0]);
          setRMinute(5);
        }}
      >
        Break
      </Button>,
    ];
    return (
      <Box
        sx={{
          display: "flex",
          "& > *": {
            m: 1,
          },
        }}
      >
        <ButtonGroup
          orientation="vertical"
          aria-label="vertical contained button group"
          variant="text"
          color="warning"
        >
          {buttons}
        </ButtonGroup>
      </Box>
    );
  }

  function VariantButtonGroup() {
    return (
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          "& > *": {
            m: 1,
          },
        }}
      >
        <ButtonGroup
          color="warning"
          variant="outlined"
          aria-label="outlined button group"
        >
          <Button
            onClick={() => {
              setOver(false);
            }}
          >
            <p>start</p>
          </Button>
          <Button onClick={() => {setPaused(!paused);}}>
            <p>{paused? <p>resume</p> : <p>pause</p>}</p>
          </Button>
          <Button
            onClick={() => {
              reset();
              setOver(true);
            }}
          >
            <p>reset</p>
          </Button>
        </ButtonGroup>
      </Box>
    );
  }

  return (
    <div className="timer">
      <Stack
        spacing={2}
        direction="row"
        divider={<Divider orientation="vertical" flexItem />}
        justifyContent="center"
        alignItems="center"
      >
        <div style={{ textAlign: "center" }}>
          <div style={{ paddingBottom: "15px" }}>
            <span>{`${h.toString().padStart(1, '0')}`}</span>
            <span>:</span>
            <span>{`${m.toString().padStart(2, '0')}`}</span>
            <span>:</span>
            <span>{`${s.toString().padStart(2, '0')}`}</span>
          </div>
          <VariantButtonGroup />
        </div>

        <div>
          <GroupOrientation />
        </div> 
      </Stack>
    </div>
  );
}

export default MyTimer;
